import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { Floating } from '../Floating';
import { useRef } from 'preact/hooks';
import { RootEnvironmentProvider } from '../../UNSAFE_Environment';

function TestComponent() {
  const buttonRef = useRef<HTMLButtonElement>(null);
  return (
    <RootEnvironmentProvider>
      <button ref={buttonRef}>Anchor</button>
      <Floating
        anchorRef={buttonRef}
        placement="bottom"
        offsetValue={{ mainAxis: 0, crossAxis: 0 }}>
        <div
          style={{
            border: '2px solid',
            backgroundColor: 'white',
            width: '100px',
            textAlign: 'center',
            padding: '10px'
          }}>
          <p>Hello World!</p>
        </div>
      </Floating>
    </RootEnvironmentProvider>
  );
}

describe('Test Floating component', () => {
  beforeEach(() => {
    if (!window.ResizeObserver) {
      // ResizeObserver isn't available in test environment
      const MockResizeObserver = jest.fn();
      MockResizeObserver.mockReturnValue({
        observe: () => null,
        unobserve: () => null,
        disconnect: () => null
      });

      Object.defineProperty(global, 'ResizeObserver', {
        writable: true,
        configurable: true,
        value: MockResizeObserver
      });
    }
  });

  it('render', async function () {
    const content = render(<TestComponent />);
    const initials = await content.getAllByText('Hello World!');
    expect(initials).not.null;
  });
});
